import React from "react";
import style from "./Payroll.module.css";
import { useEffect } from "react";

import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import UniversalTab from "../../DepartmentHead/Competetions/CompetitionTab/UniversalTab";
import { useLocation, useSearchParams } from "react-router-dom";
import SalaryHead from "./SalaryHead/SalaryHead";
import Employee from "./Employee/Employee";
import PayrollHistory from "./PayrollHistory/PayrollHistory";
import SalaryHeadInfo from "./SalaryHead/SalaryHeadInfo";
import SalaryHeadHistoryList from "./SalaryHead/SalaryHeadHistoryList";
import { useScrollbar } from "../../../../../Scroll/use-scroll";
import UserMemberContentWrapper from "../../../../../optimized/Ui/UserMember/UserMemberContentWrapper";

function Payroll({ fid, instituteId }) {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const tabList = [
    {
      name: t("payroll_staffs"),
      id: 0,
      url: "member/staff/finance?type=payroll",
    },
    {
      name: t("salary_heads"),
      id: 1,
      url: "member/staff/finance?type=payroll&n=head",
    },
    {
      name: t("history"),
      id: 2,
      url: "member/staff/finance?type=payroll&n=history",
    },
  ];
  const { scrollToTop } = useScrollbar();
  useEffect(() => {
    scrollToTop();
  }, []);

  return (
    <>
      {searchParams.get("d") === "info" || searchParams.get("f") === "staff" ? (
        ""
      ) : (
        <UserMemberContentWrapper>
          <div className={style.payroll_container}>
            <h6>{t("payroll")}</h6>
            <UniversalTab
              tabList={tabList}
              activeIndexTab={
                searchParams.get("n") === "head"
                  ? 1
                  : searchParams.get("n") === "history"
                  ? 2
                  : searchParams.get("type") === "payroll"
                  ? 0
                  : 3
              }
              // setActiveIndexTab={setActiveIndex}
              showAs={true}
              customStyleTab={{
                width: "100%",
              }}
              carryParentState={getQuery.state}
              customStyleLink={{
                width: "33.3%",
              }}
            />
            <BorderBottom
              customStyle={{
                width: "100%",
              }}
            />
          </div>
        </UserMemberContentWrapper>
      )}

      {searchParams.get("f") === "staff" ? (
        <UserMemberContentWrapper>
          <SalaryHeadHistoryList
            carryParentState={getQuery.state}
            fid={fid}
            instituteId={instituteId}
          />
        </UserMemberContentWrapper>
      ) : searchParams.get("d") === "info" ? (
        <UserMemberContentWrapper>
          <SalaryHeadInfo
            carryParentState={getQuery.state}
            fid={fid}
            instituteId={instituteId}
          />
        </UserMemberContentWrapper>
      ) : searchParams.get("n") === "history" ? (
        <UserMemberContentWrapper>
          <PayrollHistory fid={fid} instituteId={instituteId} />
        </UserMemberContentWrapper>
      ) : searchParams.get("n") === "head" ? (
        <UserMemberContentWrapper>
          <SalaryHead
            carryParentState={getQuery.state}
            fid={fid}
            instituteId={instituteId}
          />
        </UserMemberContentWrapper>
      ) : searchParams.get("type") === "payroll" ? (
        <UserMemberContentWrapper>
          <Employee fid={fid} instituteId={instituteId} />
        </UserMemberContentWrapper>
      ) : null}
    </>
  );
}

export default Payroll;
